#topBar {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 15px 20px;
  }
  
  .adjust {
    opacity: 0.4;
    font-size: 18px;
    cursor: pointer;
  }
  
  #searchBar {
    width: 70%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  #searchInput {
    border: none;
    font-weight: 100;
    margin-left: 20px;
    opacity: 0.5;
    padding: 2px 5px;
    width: 70%;
    /* padding-bottom: 2px; */
  }
  
  #searchInput:active,
  #searchInput:focus {
    outline: none;
    border-bottom: 2px solid #6100ec;
  }
  
  .icon {
    font-size: 20px;
    /* font-weight: 100; */
    opacity: 0.5;
    cursor: pointer;
  }
  
  #options {
    width: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  #options > i {
    opacity: 0.5;
  }
  
  #dropDown {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    /* width: 20%; */
  }
  
  #name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    letter-spacing: 0.2px;
    font-weight: 600;
    /* margin-left: 12px; */
  }
  
  #imgHere {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 12px;
  }
  
  #drop {
    margin-left: 3px;
  }
  
  #dev {
    opacity: 0.4;
  }
  
  #imgHere > img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  
  #menu {
    position: absolute;
    background-color: rgb(228, 234, 247);
    
    top: 70px;
    right: 20px;
    border-radius: 10px;
    padding: 10px;
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: none;
    z-index: 2;
    border: 1px solid black;
  }
   
  .dropDownItem {
    font-size: 13px;
    margin-bottom: 7px;
    color: rgb(7, 7, 7);
    border-bottom: 1px solid rgb(69, 43, 219);
    text-align: center;
    display: inline-block;
    
  }
  
